import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatPaginator, MatDialog, MatTable } from '@angular/material';
import { AccClosureMaintenanceServiceService } from '../acc-closure-maintenance-service.service';
import { AccClosureMaintAuditLog } from '../AccClosureMaintAuditLog';
import { userDefiendDTO } from '../userDefiendField';

import { accountingEntryDTO } from '../accountingEntriesDTO';
import { AccClosureMainReqDTO } from '../AccClosureMaintReqDTO';
import Swal from 'sweetalert2';






@Component({
  selector: 'app-acc-closure-transfer-amounts',
  templateUrl: './acc-closure-transfer-amounts.component.html',
  styleUrls: ['./acc-closure-transfer-amounts.component.css'],


})
export class AccClosureTransferAmountsComponent implements OnInit {
  accType: any;
  texterror:boolean;
  values:'';
  accountType: any;
  submitAccMaintDisable: boolean;
  //submitDisabled:boolean=false;

  constructor(private formBuilder: FormBuilder, private accClosureMaintService: AccClosureMaintenanceServiceService) {
  }
  currentUser: any

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  amountTransfer: FormGroup;
  pattern="[0-9]";
  // -----------Audit log------------
  auditLog = new AccClosureMaintAuditLog();
  // ---------dto's refernce--------
  userDefiendField: Array<userDefiendDTO> = [];

  accountingEntries: Array<accountingEntryDTO> = []



  // -----------Main DTO-----------
  accClosureMaintDTO = new AccClosureMainReqDTO();

  ngOnInit() {
    this.userDefiendField.push(new userDefiendDTO());

    this.accountingEntries.push(new accountingEntryDTO())
   // this.accountingEntries[0].id.serialNo
    this.amountTransfer = this.formBuilder.group({
      amountAccountType: ['', Validators.required]
    });
    this.currentUser = localStorage.getItem('userFromLogin');
  
  }

  //---------- Accounting Entries---------

  addDataOfaccountingEntries() {

    let accountingEntryObj = new accountingEntryDTO();
    //  accountingEntryObj.id.serialNo=serial
    this.accountingEntries.push(accountingEntryObj);
    console.log(this.accountingEntries);
  }
  removeDataOfaccountingEntries(i: number) {
    this.accountingEntries.splice(i, 1);
  }

  addDataOfUserDefiendField() {
    let userDefiendOBJ = new userDefiendDTO();
    this.userDefiendField.push(userDefiendOBJ);
  }
  removeDataOfUserDefiendField(i: number) {
    this.userDefiendField.splice(i, 1);
  }


  onSubmittingAccClosureMaintenance() {
// console.log(this.accountingEntries[])
    this.accClosureMaintDTO.userDefinedFields = this.userDefiendField;
    this.accClosureMaintDTO.accountingEntries = this.accountingEntries
    this.accountType = this.amountTransfer.controls.amountAccountType.value
    console.log("this is full submit", this.accountType);
    console.log(this.accClosureMaintDTO);

    this.accClosureMaintService.onfinalSubmit(this.accountType, this.currentUser, this.accClosureMaintDTO).subscribe(finalBackendResp => {
      console.log(finalBackendResp);
      this.auditLog = finalBackendResp;

      if(finalBackendResp.allFiledsRespnse == 'N' || finalBackendResp.allFiledsRespnse === 'N'){
        Swal.fire({
          title: 'Please fill in all the required fields',
          type: "warning"
        })
      }else if(finalBackendResp.accountTypeFiled == 'N'){
        Swal.fire({
          title: 'You’ve not entered any data to save',
          type: "warning"
        })
      }else if(finalBackendResp.udfAndAccountingEntriesFields == 'N'){
        Swal.fire({
          title: 'You’ve not entered any data to save',
          type: "warning"
        })
      }
      else {
      if (this.auditLog.recordStatus == 'O') {
        this.auditLog.recordStatus = 'OPEN'
      }
      if (this.auditLog.verifiedStatus == 'U') {
        this.auditLog.verifiedStatus = 'UNAUTHORIZED'
      }
      if (this.auditLog.verifiedOnce == 'N') {
        this.auditLog.verifiedOnce = 'NO'
      }
      if (!this.auditLog) {
        Swal.fire({
          title: 'Record Is Already Exist',
          type: "warning"
        })
      }
      // else if(this.auditLog.allFiledsRespnse == 'N'){
      //   Swal.fire({
      //     title: 'You’ve not entered any data to save!',
      //     type: "warning"
      //   })
      // }
      else {
       // this.submitDisabled=true;
        Swal.fire({
          title: 'Record Saved Successfully',
          
          // type: "success"
        });
        this.submitAccMaintDisable=true;
      }
    }
    })
  

  }
 
}