import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource } from '@angular/material';

import { AccClosureMaintenanceServiceService } from '../acc-closure-maintenance-service.service';
import { AccClosureMainReqDTO } from '../AccClosureMaintReqDTO';
import { accClosureMainSummaryDTO } from './accClosureMaintSummaryDTO';
import { NavigationExtras, Router } from '@angular/router';
import { RoleService } from '../../roles1/roles.service';
import { permissionsLabels } from '../../roles1/models/fmosNewRolePermissions';

  

@Component({
  selector: 'app-acc-closure-maintenance-summary',
  templateUrl: './acc-closure-maintenance-summary.component.html',
  styleUrls: ['./acc-closure-maintenance-summary.component.css']
})
export class AccClosureMaintenanceSummaryComponent implements OnInit {
  bankdetailsresp: any;
 
  dataSource: MatTableDataSource<unknown>;

  constructor(private accClosureService:AccClosureMaintenanceServiceService,private roleService: RoleService, private router: Router) { }
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  displayedColumns: string[] = ['accountType', 'inputBy'];
 accMaintReq =new AccClosureMainReqDTO();
 accClosuresummaryDto:any;
 roleCodes = new permissionsLabels();
  
  ngOnInit() {
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);

    this.getAccountClosureMaintenancesummary()
  }
  newRolePermissions()
  {
    this.roleService.fetchScreenPermissions('Acc Closure Maintenance');
  }

getAccountClosureMaintenancesummary()
{
  console.log("page loads this data should come");
  this.accClosureService.gettingAccountClosureMaintenance().subscribe(summaryResp=>{
    console.log(summaryResp);
this.accClosuresummaryDto=summaryResp;
console.log("this is data",this.accClosuresummaryDto);
this.dataSource = new MatTableDataSource(this.accClosuresummaryDto);
this.dataSource.paginator = this.paginator
  })
}

getAccountType(row)
{
  console.log("this is in summary to ediit account Closure aintenace",row);
  console.log(row.accountType);
  this.accClosureService.gettingcompleteData(row.accountType).subscribe(completeData=>{
    console.log(completeData);
    this.accMaintReq=completeData;
    localStorage.setItem("accType",row.accountType);
    const navigationExtras: NavigationExtras = {
      queryParams: {
        accountTypeData: row.accountType,
        maker:row.inputBy,
        makerTime:row.inputTime,
        record:row.recordStatus,
        onceVerified:row.verifiedOnce,
        authstatus:row.verifiedStatus,
        authorizedBy:row.verifiedBy,
        authtime:row.verifiedTime,
        modfnNo:row.modNo

      }};
//  /-----------this is for all tabs and user defiend field--------------
    let allDetailsResp=JSON.stringify(this.accMaintReq)
   
    localStorage.setItem("allDataResp",allDetailsResp);
    console.log(allDetailsResp);
      this.router.navigate(['/account-closure-maintenance/edit'],navigationExtras
      );
  })
  
}
}
