import { Injectable } from '@angular/core';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { HttpClient } from '@angular/common/http';
import { AccClosureMaintAuditLog } from './AccClosureMaintAuditLog';
import { AccClosureMainReqDTO } from './AccClosureMaintReqDTO';
import { accClosureMainSummaryDTO } from './acc-closure-maintenance-summary/accClosureMaintSummaryDTO';
import { retry } from 'rxjs/operators';

export const API_URL = AppConstants.acctCloserUrl;
@Injectable({
  providedIn: 'root'
})
export class AccClosureMaintenanceServiceService {

  constructor(private http: HttpClient) { }


  // ----------------final submit
  onfinalSubmit(accType: any, maker: string, data) {
    //return this.http.post<AccClosureMaintAuditLog>(`${API_URL}/accClosureMaint/save/${accType}/${maker}`,data);
    if (accType == '') {
      return this.http.post<AccClosureMaintAuditLog>(`${API_URL}/accClosureMaint/save/null/${maker}`, data);
    } else {
      return this.http.post<AccClosureMaintAuditLog>(`${API_URL}/accClosureMaint/save/${accType}/${maker}`, data);
    }
  }
  gettingAccountClosureMaintenance() {
    return this.http.get<accClosureMainSummaryDTO>(`${API_URL}/accClosureMaint/getSummary`)
  }
  gettingcompleteData(accountType: string) {
    return this.http.get<AccClosureMainReqDTO>(`${API_URL}/accClosureMaint/getAccountTypeRecords/${accountType}`)
  }
  submittingTheEditedRecord(data, acctype: string, maker: string) {
    console.log("in sevice", data);
    return this.http.put<any>(`${API_URL}/accClosureMaint/modify/${acctype}/${maker}`, data)
  }
  onAuthofAccMaint(acctype: string, maker: string) {
    return this.http.get<accClosureMainSummaryDTO>(`${API_URL}/accClosureMaint/verify/${acctype}/${maker}`)
  }
  deletingParticularRecord(id: number, type: string, acctype: string, maker: string) {
    return this.http.delete<accClosureMainSummaryDTO>(`${API_URL}/accClosureMaint/delete/${id}/${type}/${acctype}/${maker}`)
  }
  onClosingTheRecord(acctype: string, maker: string) {
    return this.http.get<accClosureMainSummaryDTO>(`${API_URL}/accClosureMaint/close/${acctype}/${maker}`)
  }
  onReopningTheRecord(acctype: string, maker: string) {
    return this.http.get<accClosureMainSummaryDTO>(`${API_URL}/accClosureMaint/reopen/${acctype}/${maker}`)
  }
}
